.weatherDataCont {
  background-color: rgba(200, 200, 200, 0.5);
  width: 100%;
  height: 100px;
  line-height: 100px;
  border-radius: 15px;
  padding: 5px;
  box-sizing: border-box;
}

.weatherExtraInfoCont {
  width: 40%;
  display: inline-block;
  line-height: 28px;
  text-align: left;
}

.weatherTemperatureCont {
  width: 60%;
  display: inline-block;
}

.temperatue {
  font-size: 28px;
  font-weight: bold;
  padding-left: 20px;
  color: #333;
}

.temperatueType  {
  font-weight: bold;
  color: #666;
}

.extraInfoText {
  display: block;
  color: #1535c5;
  font-size: 16px;
  font-weight: bold;
}

.wicon {
  color: rgb(255, 72, 0);
}
