body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.locationCont {
  width: 100%;
  height: 35px;
  text-align: left;
  color: #888;
  line-height: 35px;
  overflow: hidden;
}

.locationCont h1 {
  margin: 0;
  white-space: nowrap;
}

.weatherLocationCont {
  width: calc(100% -20px);
  margin-left: 10px;
  margin-right: 10px;
  min-width: 350px;
  box-sizing: border-box;
  padding: 15px;
  font-family: "Roboto", sans-serif;
  box-shadow: 10px 10px 40px -12px rgba(0, 0, 00.75);
  cursor: pointer;
}

.weatherDataCont {
  background-color: rgba(200, 200, 200, 0.5);
  width: 100%;
  height: 100px;
  line-height: 100px;
  border-radius: 15px;
  padding: 5px;
  box-sizing: border-box;
}

.weatherExtraInfoCont {
  width: 40%;
  display: inline-block;
  line-height: 28px;
  text-align: left;
}

.weatherTemperatureCont {
  width: 60%;
  display: inline-block;
}

.temperatue {
  font-size: 28px;
  font-weight: bold;
  padding-left: 20px;
  color: #333;
}

.temperatueType  {
  font-weight: bold;
  color: #666;
}

.extraInfoText {
  display: block;
  color: #1535c5;
  font-size: 16px;
  font-weight: bold;
}

.wicon {
  color: rgb(255, 72, 0);
}

.LocationList {
  height: 85vh;
  overflow: auto;
}

@media (max-width: 64em) {
  .LocationList {
    max-height: 40vh;
    margin-bottom: 20px;
  }
}

.forecast-title {
  color: white;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.details {
  background: #999;
  height: 85vh;
  width: calc(100% - 40px);
  overflow: auto;
  padding-right: 20px;
  padding-left: 20px;
}

@media (max-width: 64em) {
  .details {
    height: 40vh;
  }
}

